<template>
  
    <div class="tab-wrap">
      <TabBtnItem :data="tabData" :currentTab="currentTab" @update:tab="updateTab">
        <template #panel="{ tab }">
          
            <Transition name="fade" mode="out-in">
              <component :is="tabContMap[tab]"></component>
            </Transition>
          
        </template>
      </TabBtnItem>
    </div>

</template>

<script setup>
import { ref } from 'vue'
import TabBtnItem from '@/components/TabBtnItem';
import TabContentA from '@/view/TabContentA';
import TabContentB from '@/view/TabContentB';

const tabData = [
  {name : 'tab1'},
  {name : 'tab2'}
];

const currentTab = ref(tabData[1].name);
const tabContMap = {
  tab1: TabContentA,
  tab2: TabContentB,
}

const updateTab = (tabName) => {
  currentTab.value = tabName;
}
</script>