<template>

  <div class="tab-buttons">
    <button 
      type="button" 
      v-for="tab in props.data"
      :key="tab.name"
      :class="['btn', { active: isActive(tab.name) }]"
      :aria-selected="isActive(tab.name) ? 'true' : 'false'"
      @click="selectTab(tab.name)">
      <span>{{ tab.name }}</span>
    </button>
  </div>

  <div class="tab-panel">
    <slot name="panel" :tab="props.currentTab"></slot>
  </div>
 
</template>
<script setup>
import { defineProps, defineEmits } from 'vue'

const props = defineProps({
  data: Array,
  currentTab: String
})

const emit = defineEmits(['update:tab'])
const selectTab = (tabName) => {
  emit('update:tab', tabName)
}
const isActive = (tabName) => {
  return tabName === props.currentTab;
};
</script>